import './App.css';

import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
//  import Stack from '@mui/material/Stack';
import Box from '@mui/material/Box';
import { useState, useEffect } from 'react';
import useMediaQuery from '@mui/material/useMediaQuery';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import LoginButton from './components/LoginButton';
import SignupButton from './components/SignupButton';
import commonUtils from './utils';

//  import Background from './components/background';
import Main from './components/main';
import b1 from './static/images/b1.webp';
import b2 from './static/images/b2.webp';
import b3 from './static/images/b3.webp';
import b4 from './static/images/b4.webp';
import b12000 from './static/images/b1-2000.webp';
import b22000 from './static/images/b2-2000.webp';
import b32000 from './static/images/b3-2000.webp';
import b42000 from './static/images/b4-2000.webp';

function MyMemoriesButton() {
  const abovesm = useMediaQuery((theme) => theme.breakpoints.up('sm'));
  return !abovesm ? (
    <Button
      type="submit"
      variant="contained"
      sx={{
        position: 'fixed',
        top: 'calc(100vh - 150px)',
        left: '50vw',
        transform: 'translate(-50%, 0)',
        borderRadius: '5px',
        backgroundColor: 'white',
        '&:hover': {
          backgroundColor: 'form.input',
        },
      }}
    >
      <Link style={{ textDecoration: 'none' }} to="/memories">
        <Typography variant="h4" color="primary">Go to My Memories</Typography>
      </Link>
    </Button>
  ) : (
    <Button
      type="submit"
      variant="contained"
      sx={{
        position: 'fixed',
        top: 20,
        right: 20,
        borderRadius: '25px',
        backgroundColor: 'white',
        '&:hover': {
          backgroundColor: 'form.input',
        },
      }}
    >
      <Link style={{ textDecoration: 'none' }} to="/memories">
        <Typography variant="h6" color="primary">Go to My Memories</Typography>
      </Link>
    </Button>

  );
}

function Actions({ authURL }) {
  const cookieValue = commonUtils.getUserInfo();
  const isLoggedIn = cookieValue !== '';
  if (isLoggedIn === true) {
    return (
      <MyMemoriesButton />
    );
  }
  return (
    <>
      <LoginButton authURL={authURL} />
      <SignupButton authURL={authURL} />
    </>
  );
}

function App({ authURL }) {
  const urls = [
    [b1, `${b12000} 1500w, ${b1} 2000w`],
    [b2, `${b22000} 1500w, ${b2} 2000w`],
    [b3, `${b32000} 1500w, ${b3} 2000w`],
    [b4, `${b42000} 1500w, ${b4} 2000w`],
  ];

  const [image, setImage] = useState(0);
  const [display, setDisplay] = useState('none');

  useEffect(() => {
    const timer = setTimeout(() => setImage((index) => (index + 1) % urls.length), 5000);
    return () => clearTimeout(timer);
  });
  return (
    <div className="App" style={{ display, backgroundColor: 'rgb(255, 253, 246)' }}>
      <Box
        component="img"
        src={urls[image][0]}
        srcSet={urls[image][1]}
        alt="Background"
        width="100%"
        height="100vh"
        onLoad={() => setDisplay('block')}
        sx={{
          positon: 'absolute',
          top: 0,
          bottom: 0,
          objectFit: 'cover',
        }}
      />
      <Actions authURL={authURL} />
      <Main authURL={authURL} />
    </div>
  );
}

App.propTypes = {
  authURL: PropTypes.string.isRequired,
};

Actions.propTypes = {
  authURL: PropTypes.string.isRequired,
};

export default App;
